.styleNavbar ul {
  list-style-type: none;
}
.linkNavbar {
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 5px 7px;
  border-radius: 10px 10px 0px 0px;
  position: relative;
  transform: translateY(-0.3rem);
  transition: 0.5s;
  color: var(--red-color);
}
.activeIcon {
  transform: translateY(0.1rem);
  background-color: white;
  color: black;
}
.activeIconMobile {
  transform: translateY(-0.1rem);
  background-color: white;
  color: black;
}
a:hover {
  color: black;
  background-color: white;
}
@media (max-width: 767.98px) {
  a:hover {
    color: black;
  }
}

