:root {
  --light: #f0f0f0;
  --primary: #292b33;
  --secondary: #d8d7d4;
  --red-color: #ed213a;
}
@import url("https://fonts.googleapis.com/css2?family=Marck+Script&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cantarell&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Srisakdi&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bad+Script&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Maven+Pro&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100&display=swap");
@font-face {
  font-family: "Game_Of_Squids";
  src: url("./Font/Game\ Of\ Squids.ttf");
}

@font-face {
  font-family: "Zamaica";
  src: url("./Font/Zamaica.ttf");
}
@font-face {
  font-family: "futural";
  src: url("./Font/futural.ttf");
}
@font-face {
  font-family: "futurali";
  src: url("./Font/futurali.ttf");
}
@font-face {
  font-family: "ClickerScript-Regular";
  src: url("./Font/ClickerScript-Regular.ttf");
}
a {
  text-decoration: none;
}
.text-red-color {
  color: var(--red-color);
}
.text-primary-color {
  color: var(--primary);
}
.text-red-color:hover {
  color: var(--red-color);
}
.w-80 {
  width: 80%;
}
.w-60 {
  width: 60%;
}
.w-40 {
  width: 40%;
}
.w-30 {
  width: 30%;
}
.w-20 {
  width: 20%;
}
.positionTopPage {
  padding-top: 8rem;
}
@media (min-width: 767.98px) {
  .positionTopPage {
    min-height: calc(100vh - 133px);
  }
}
@media (max-width: 767.98px) {
  .positionTopPage {
    padding-top: 3rem;
  }
}
.border-red {
  border-color: var(--red-color) !important;
}
